@import url('https://fonts.googleapis.com/css2?family=Baloo+Bhai+2:wght@600&family=Balsamiq+Sans&family=My+Soul&family=Outfit:wght@100;200;300;400;500;600;700;800;900&family=Ubuntu:wght@300&family=Whisper&display=swap');

*{
  box-sizing: border-box;
  margin: 0;
  padding:0;
  
  
font-family: 'Outfit', sans-serif;

}

body{
  background: grey;
}


h1,h4,a,p{
color: white;
text-decoration: none;
}

ul{
  list-style-type: none; 
}
.btn{
  padding: 12px 32px;
  font-size: 1 rem;
  text-transform: uppercase;
  background: rgb(248,217,15);
  color: #222;
  border: 1px solid #fff;
  font-weight:600;
  cursor: pointer;
}

.btn-light{
  background: transparent;
  color: #fff;
}
.btn:hover{
  background: rgba(255, 255, 255, 0.2);
  color: #fff;
  transition: 0.3s;
}