.hero-img{
    width:100%;
    background: rgba(0, 0, 0, 0.5) ;
    height: 60vh;
    position: relative;
}

.hero-img::before{
    content:"";
    background: url("https://images.unsplash.com/photo-1544256718-3bcf237f3974?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxzZWFyY2h8Mnx8cHJvZ3JhbWluZ3xlbnwwfHwwfHw%3D&auto=format&fit=crop&w=900&q=60");
    background-size: cover;
    background-position: center top;
    height: 100%;
    width:100%;
    left:0;
    top:0;
    position: absolute;
    z-index: -1;
}

.heading{
    width: 100%;
    height:100%;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.hero-img h1{
    font-size: 2.4rem;
}
.hero-img p{
    font-size: 1.4rem;
    text-align: center;
}

@media screen and (max-width:640px){
    .hero-img h1{
        font-size: 2rem;
    }
    
}